import Vue from 'vue'
import Router from 'vue-router'
import pointNewAndEdit from '@/components/page/pointNewAndEdit.vue'
import pointRecord from '@/components/page/pointRecord.vue'
import pointContact from '@/components/page/pointContact.vue'
import buildNameSearch from '@/components/page/buildNameSearch.vue'

Vue.use(Router)
/* eslint-disable */
export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/point'
    },
    {
      path: '/point',
      name: 'pointNewAndEdit',
      component: pointNewAndEdit
    },
    {
      path: '/record',
      name: 'pointRecord',
      component: pointRecord
    },
    {
      path: '/contact',
      name: 'pointContact',
      component: pointContact
    },
    {
      path: '/search',
      name: 'buildNameSearch',
      component: buildNameSearch
    }
  ]
})



