import './components/assets/css/normalize.css'
import './components/assets/css/style.css'
import './components/assets/css/excess.css'
// import 'mint-ui/lib/style.css'
import Vue from 'vue'
import verify from 'vue-verify-plugin'
import App from './App'
import router from './router'
import { Swipe, SwipeItem, Tabbar, TabItem, CellSwipe, Search, Navbar, TabContainer, TabContainerItem, Picker, Cell, Loadmore } from 'mint-ui'
Vue.component(Swipe.name, Swipe)
Vue.component(Search.name, Search)
Vue.component(SwipeItem.name, SwipeItem)
Vue.component(Tabbar.name, Tabbar)
Vue.component(TabItem.name, TabItem)
Vue.component(CellSwipe.name, CellSwipe)
Vue.component(Navbar.name, Navbar)
Vue.component(TabContainer.name, TabContainer)
Vue.component(TabContainerItem.name, TabContainerItem)
Vue.component(Picker.name, Picker)
Vue.component(Cell.name, Cell)
Vue.component(Loadmore.name, Loadmore)
Vue.config.productionTip = false
Vue.use(verify, {blur: true})
// Vue.use(verify, {blur: true})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



