        let f16 = f16Arr[this.company.checkServingAfternoonTea];
        //是否有其他自助设备
        let f17Arr = [3,1];
        let f17 = f17Arr[this.company.checkSelfServiceEquipment];

        score = c7+c8+c9+c10+c11+c12+c13+c14+c15+c16+c17+f7+f8+f9+f10+f11+f12+f13+f14+f15+f16+f17;
      } else if (typeName=="医院"){
        //医院类型
        let c8Arr = [20,15,10];
        let c8 = c8Arr[this.hospital.checkHospitalType];
        //医护人员数量
        let c9 = this.hospital.medicalNum>=5000?30:this.hospital.medicalNum>=3000?25:this.hospital.medicalNum>=1800?20:this.hospital.medicalNum>=1000?10:5;
        //门诊接待量
        let c10 = this.hospital.receptionNum>=20000?25:this.hospital.receptionNum>=10000?20:this.hospital.receptionNum>=7000?15:this.hospital.receptionNum>=5000?12:this.hospital.receptionNum>=3000?9:5;
        //院内商业
        let c11Arr = [3,1];
        let c11 = c11Arr[this.hospital.checkHospitalBusiness];
        //便利店
        let c12Arr = [-5,0,3];
        let c12 = c12Arr[this.hospital.checkHospitalCoffeeshop];
        //楼宇属性
        let f8Arr = [15,12,5,5,8];
        let f8 = f8Arr[this.hospital.checkBuildingAttribute];
        //楼宇层高
        let f9Arr = [5,3,1];
        let f9 = f9Arr[this.hospital.checkBuildingHeight];
        //点位人流量
        let f10 = this.hospital.checkHospitalHumanTraffic>=5000?20:this.hospital.checkHospitalHumanTraffic>=3000?10:this.hospital.checkHospitalHumanTraffic>=1800?8:this.hospital.checkHospitalHumanTraffic>=1000?5:0;
        //设备摆放位置
        let f11Arr = [10,8,6,4,0];
        let f11 = f11Arr[this.hospital.checkHospitalEquipmentLocation];
        //是否有其他设备
        let f12Arr = [3,1];
        let f12 = f12Arr[this.hospital.checkHospitalHasOtherEquipment];

        score = c8+c9+c10+c11+c12+f8+f9+f10+f11+f12;
      } else {
        score = 0;
      }
      if (typeName=="写字楼"){
        this.pointLevel = score>=105?1:score>=88?2:score>=80?3:4;
      } else if (typeName=="园区"){
        this.pointLevel = score>=110?1:score>=85?2:score>=75?3:4;
      } else if (typeName=="学校"){
        this.pointLevel = score>=88?1:score>=82?2:score>=70?3:4;
      } else if (typeName=="公司"){
        this.pointLevel = score>=92?1:score>=82?2:score>=70?3:4;
      } else if (typeName=="医院"){
        this.pointLevel = score>=92?1:score>=82?2:score>=70?3:4;
      } else {
        this.pointLevel = score>=92?1:score>=82?2:score>=70?3:4;
      }
      if(isNaN(score)) {
        score = 0;
      }
      return score;
    }
