          wx.chooseImage({
            count: 1, // 默认9
            sizeType: ['compressed'], // 可以指定是原图还是压缩图，默认二者都有
            sourceType: ['camera'], // 可以指定来源是相册还是相机，默认二者都有
            success: function (res) {
              console.log('chooseImage res',res);
              localIds = res.localIds;
              let imgLocalId = localIds[0];
              wx.uploadImage({
                localId: localIds.toString(),
                isShowProgressTip: 1,
                success: function(res) {
                  let mediaid = res.serverId;
                  console.log('mediaid',mediaid);
                  console.log("uploadImage res",res);
                  console.log("imgLocalId..",imgLocalId);
                  if (i<=2){
                    _this["imgInitUrl"+i] = imgLocalId;
                    _this.buildingImgUrlData[i-1] = mediaid;
                  } else if (i<=4){
                    _this["imgInitUrl"+i] = imgLocalId;
                    _this.hallImgUrlData[i-3] = mediaid;
                  } else if (i<=6){
                    _this["imgPositon"+i] = imgLocalId;
                    _this.form.pointPositionPic[i-5] = mediaid;
                  } else if (i==7){
                    _this["imgLicense"+i] = imgLocalId;
                    _this.form.pointLicencePic[i-7] = mediaid;
                  } else if (i<=12){
                    _this["imgCompany"+i] = imgLocalId;
                    _this.form.pointCompanyPic[i-8] = mediaid;
                  } else if (i==13){
                    _this["imgFlat"+i] = imgLocalId;
                    _this.form.pointPlan[i-13] = mediaid;
                  }
                },
                fail: function(data){
                  console.log('error',data);
                }
              })
            },
            fail: function(data){
              console.log('error',data);
            }
          });
        }
      });
    },
    //保存草稿
    saveData() {
      if (this.buildType==""||this.form.buildId==""||this.form.position==""){
        this.alertMsg("渠道、楼宇名称、摆放位置不能为空");
        return;
      } else {
        this.submitAction("0");
      }
    },
    createdData() {
      //验证分组(根据渠道类型进行分组，根据当前选择的渠道，进行验证对应字段)
      let buildType = this.buildTypeValue;
      if (this.buildType==""||this.form.buildId==""||this.form.position==""){
        this.alertMsg("渠道、楼宇名称、摆放位置不能为空");
        return;
      }
      if (!this.$verify.check("public")){
        this.alertMsg('请完善基础信息');
        return;
      } else if (this.buildTypeValue=='学校'&&!this.$verify.check("publicSchool")){
        this.alertMsg('请完善基础信息');
        return;
      } else if (this.buildTypeValue=='公司'&&!this.$verify.check("publicCompany")){
        this.alertMsg('请完善基础信息');
        return;
      } else if (this.buildTypeValue=='医院'&&!this.$verify.check("publicHospital")){
        this.alertMsg('请完善基础信息');
        return;
      } else if (!this.checkPhone(this.form.contactTel)){
        this.alertMsg('请填写正确的电话号码');
        return;
      } else if (this.buildTypeValue=='写字楼'&&!this.$verify.check("formOffice")){
        this.alertMsg('请完善评分相关信息');
        return;
      } else if (this.buildTypeValue=='园区'&&!this.$verify.check("formPark")){
        this.alertMsg('请完善评分相关信息');
        return;
      } else if (!this.$verify.check("other")){
        this.alertMsg('请完善其他相关信息');
        return;
      } else {
        if (this.imgInitUrl1==addImgUrl||this.imgInitUrl2==addImgUrl) {
          this.alertMsg('请上传楼宇外观照片');
          return false;
        }
        if (this.imgInitUrl3==addImgUrl||this.imgInitUrl4==addImgUrl) {
          this.alertMsg('请上传大厅全景照片');
          return false;
        }
        if (this.imgPositon5==addImgUrl||this.imgPositon6==addImgUrl) {
          this.alertMsg('请上传摆放位置照片');
          return false;
        }
        if (this.imgLicense7==addImgUrl) {
          this.alertMsg('请上传清晰的水牌照片');
          return false;
        }
        if (this.imgCompany8==addImgUrl||this.imgCompany9==addImgUrl||this.imgCompany10==addImgUrl||this.imgCompany11==addImgUrl||this.imgCompany12==addImgUrl) {
          this.alertMsg('请上传楼上公司照片');
          return false;
        }
        // if (this.imgFlat13==addImgUrl) {
        //   this.alertMsg('请上传平面图照片');
        //   return false;
        // }
        this.submitAction("1");
      }
    },
    submitAction(type){
      this.getBuildNameById();
      console.log("this.form.pointName...",this.form.pointName);
      // return false;
      //type 0保存草稿、1创建
      const buildType=this.buildTypeValue;//渠道类型名称
       // 条件
      const conditionInfoBuild={//写字楼
        floorArea:this.form.floorArea,//平层面积
        buildFloor:this.form.buildFloor,//楼层
        lodgingRatio:this.form.lodgingRatio,//入住率
        useRatio:this.form.useRatio,//使用率
        buildNum:this.form.buildNum//楼宇人数
      }
      const conditionInfoPark={//园区
        floorParkArea:this.park.floorParkArea,//平层面积
        buildParkFloor:this.park.buildParkFloor,//楼层
        lodgingParkRatio:this.park.lodgingParkRatio,//入住率
        useParkRatio:this.park.useParkRatio,//覆盖楼宇
        coverParkNum:this.park.coverParkNum//覆盖人数
      }
      const otherInfo={//其他信息
        unicorn:this.form.unicorn,//联通
        mobile:this.form.mobile,//移动
        telecom:this.form.telecom,//电信
        power:this.form.power,//电源情况
        putEnvironment:this.form.putEnvironment,//投放环境
        competingGoods:this.form.competingGoods//竞品情况
      }
      const baseInfo={//基础信息
        rentWay:this.form.rentWay,//租金方式
        rentNum:this.form.rentNum,//对应金额
        electric:this.form.electric,//电费
        service:this.form.service,//服务费
        total:this.form.total,//费用总额
      }
      let params={
        point_id:this.id,//点位Id(新建时为空，编辑时传输对应值)
        point_name:this.form.pointName+this.form.position,
        point_position:this.form.position,//摆放位置
        point_level: this.pointLevel,//点位级别
        point_score:this.pointScore,//点位分数
        cooperate:this.form.cooperation,//合作方式
        point_status:type,//保存草稿
        build_type_id: this.buildType,//渠道类型,
        build_record_id:this.form.buildId,//楼宇ID
        point_basic_info: baseInfo,
        point_score_info:"",//条件信息(只有写字楼、园区存在未有的条件，其他渠道均不需要传)
        point_other_info: otherInfo,//其他信息
        // buildAppearPic[0];buildHallPic,buildingImgUrlData,hallImgUrlData
        point_licence_pic:this.form.pointLicencePic,//清晰水牌
        point_position_pic:this.form.pointPositionPic,//摆放位置图片
        point_company_pic:this.form.pointCompanyPic,//公司照片
        point_plan:this.form.pointPlan,//平面图
        contact_tel:this.form.contactTel,//联系电话
        contact_name:this.form.contactName//联系人
      }
      if (buildType=="写字楼"){
        params.point_score_info=conditionInfoBuild;//条件信息
      } else if (buildType=="园区"){
        params.point_score_info=conditionInfoPark;//条件信息
      } else if (buildType=="学校"){
        baseInfo.area=this.form.area;//楼宇占地面积
        baseInfo.floor=this.form.floor;//楼宇层高
        baseInfo.humanTraffic=this.form.humanTraffic;//点位人流量
      } else if (buildType=="公司"){
        baseInfo.companyType=this.form.companyType;//公司类型
        baseInfo.companyNum=this.form.companyNum;//公司人数
      } else if (buildType=="医院"){
        baseInfo.hospitalLevel=this.form.hospitalLevel;//医院等级
        // baseInfo.receptionNum=this.form.receptionNum;//门诊接待量
        // baseInfo.medicalNum=this.form.medicalNum;//医护人员数量
        // baseInfo.hospitalHuman=this.form.hospitalHuman;//医院点位人流量
      }
      console.log("params",params);
      axios.post(this.submitFormUrl,params)
      .then((response)=> {
        let data = response.data;
        console.log("data",data);
        if (data.error_code==0){
          if (type=="0"){
             this.alertMsg('保存草稿成功');
          } else {
            this.alertMsg('创建成功');
          }
          // this.$router.push('/record');
          window.location.href = "/point-evaluation/index?type=record";
        } else {
           this.alertMsg(data.msg);
        }
      })
      .catch((error)=> {
        this.alertMsg('接口错误');
      });
    },
    alertMsg(msg,type)
    {
      // let msgType = type?type:"error";
      MessageBox.alert('',{
        title: '',
        message: msg,
        confirmButtonText:'知道了'
      });
    },
    //修改渠道类型
    changeBuildType(event,id) {
      if (event!==undefined) {
        this.buildType= event.target.value;
        this.form.buildId="";
      }
      // console.log("id..",id)
      if (id!==undefined) {
        this.buildType= id;
      }
      this.getBuildTypeNameById();
      console.log("this.buildType..",this.buildType);
      axios.get('/point-evaluation/get-build-list?build_type_id='+this.buildType).then((res)=>{
        let initData = res.data;
        console.log("get-build-list data",initData);
        if (initData.error_code ==0){
          if (initData.data.length>0){
             this.form.buildingNameList=initData.data;//楼宇名称列表
          } else {
            this.alertMsg("暂无列表");
            this.form.buildingNameList=[];
          }
        } else {
          this.alertMsg("暂无列表");
          this.form.buildingNameList=[];
        }
      })
      .catch((error)=>{
        console.log("接口错误");
        this.form.buildingNameList=[];
      });
    },
    //根据楼宇ID,获取楼宇信息
    getInfoByBuildingName(){
      axios.get('/point-evaluation/get-build-record-info?record_id='+this.form.buildId).then((res)=>{
        const initData =res.data;
        console.log("楼宇信息",initData);
        if (initData.error_code== 0){
          this.renderBuildInfo(initData.data);//渲染楼宇信息
        } else {
          this.alertMsg(initData.msg);
          return false;
        }
      }).catch((error)=>{
         console.log("error..",error);
      });
    },
    //根据楼宇ID获取楼宇名称
    getBuildNameById(){
      console.log("this.form.buildingNameList..",this.form.buildingNameList);
      for(let i of this.form.buildingNameList){
        if (i.id==this.form.buildId){
          this.form.pointName=i.value.split('+')[0];
          break;
        }
      }
    },
    goBackMain() {
      this.goBack();
    },
    getQueryString(name) {
        var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)", "i");
        var r = window.location.search.substr(1).match(reg);
        if (r != null) {
            return decodeURI(r[2])
        }
        return null
    }
  }
}
</script>
