<template>
    <div class="yh-cont" v-show="showMain">
      <div class="header-massage">
        <div class="header-massage-first">
          <span>渠道</span>
          <select v-model="buildType" @change="showDifferenceList(-1,$event)">
            <option value="">请选择</option>
            <option :label="item.type_name" :value="item.id" v-for="(item,index) in buildTypeList" :key="index">{{item.type_name}}</option>
          </select>
        </div>
        <div class="header-massage-second">
          <span>审核状态 &nbsp;</span>
          <select v-model="pointStatus" @change="showDifferenceList(-1,$event)">
            <option value="">请选择</option>
            <option :label="item.name" :value="item.id" v-for="(item,index) in pointStatusList" :key="index">{{item.name}}</option>
          </select>
        </div>
      </div>
      <!-- 上拉刷新 -->
      <div class="more-data">
        <mt-loadmore :top-method="loadTop" :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" ref="loadmore" :auto-fill="false">
            <div>
              <!-- 循环列表 -->
              <div class="search-result" v-for="(item,index) in pointList" :key="index">
                      <ul>
                        <li>{{item.point_name}}<span style="float:right">{{item.point_status}}&nbsp;&nbsp;&nbsp;</span></li>
                        <li>渠道： {{item.type_name}}</li>
                        <li>级别：{{item.point_level}}</li>
                        <li>提交时间：{{item.created_at}}</li>
                      </ul>
                      <span class="record-status">{{item.building_status}}</span>
                      <button class="see-detail" @click="editData(index,1)">查看</button>
                      <button class="edit-data" @click="editData(index,2)">编辑</button>
              </div>
            </div>
            <div class="noInner" v-if="!haveMore">暂无更多内容喽！</div>
        </mt-loadmore>
      </div>
    </div>
</template>

<script>
/* eslint-disable */
// import Vue from 'vue'
import axios from 'axios'
// import InfiniteLoading from 'vue-infinite-loading';
import { Indicator } from 'mint-ui'
import { MessageBox } from 'mint-ui'
import { Loadmore } from 'mint-ui'

export default {
  data () {
    return {
      showMain: true,
      // keyWord: '',
      buildType: '',
      buildTypeList: [],
      pointStatus: '',
      pointStatusList: [],
      // buildRange: '',
      // buildRangeList: [],
      pointList: [],
      editUrl: '/point-evaluation/update',
      detailUrl: '/point-evaluation/view',
      searchUrl:'/point-evaluation/search-point-list',
      creatorName: '',
      pageval:20,//每次请求的个数
      pagesize:0,//当前页数
      creater_id:"",
      allLoaded:false,//是否加载完毕
      haveMore: true, //是否有更多数据
    }
  },
  computed: {
  },
  watch: { // 监视双向绑定的数据数组
  },
  mounted() {
    this.init();
    // this.loaddata();
  },
  destroyed() {
  },
  methods: {
    init() {
      this.buildTypeList = rootData.buildTypeList;
      for(let item in rootData.pointStatus){
        this.pointStatusList.push({id:item,name:rootData.pointStatus[item]})
      }
      this.pointList = rootData.pointList.pointArray;
      console.log("pointList..",this.pointList);
    },
    seeDetail(index) {
      console.log("查看");
      const point_id = this.pointList[index].id;
      axios.get(this.detailUrl+'?id='+this.pointList[index].id).then((res)=>{
        let initData = res.data.recordInfo;
        if(initData.error_code){
          MessageBox.alert('',{
            title:'',
            message: initData.msg,
            confirmButtonText:'知道了'
          });
        }else{
          console.log(initData);
          this.$router.push({
            name: 'pointNewAndEdit',
            query: {
              id: 1,
              data: initData,
              recordId: this.pointList[index].record_id
            }
          })
        }
      })
      .catch((error)=>{
        console.log("error..",error);
      });
    },
    editData(index,id) {
      console.log("编辑");
      const point_id = this.pointList[index].id;
      axios.get(this.editUrl+'?point_id='+point_id).then((res)=>{
        console.log("edit res",res.data);
        let initData = res.data;
        if(initData.error_code){
          MessageBox.alert('',{
            title:'',
            message: initData.msg,
            confirmButtonText:'知道了'
          });
        }else{
          console.log(initData);
          this.$router.push({
            name: 'pointNewAndEdit',
            query: {
              id: id,
              // data: initData,
              recordId: point_id
            }
          })
        }
      })
      .catch((error)=>{
        console.log("error..",error);
      });
    },
    showDifferenceList() {
      this.loaddata(1);
    },
    alertMsg(msg,type)
    {
      let msgType = type?type:"error";
      MessageBox.alert('',{
        title: '',
        message: msg,
        confirmButtonText:'知道了'
      });
    },
    loadBottom: function(){//上拉加载更多
       console.log("loadBottom");
       this.more();
       this.$refs.loadmore.onBottomLoaded();
      },
    loadTop: function(){//下拉刷新
      console.log("loadTop");
      this.pagesize = 0;
      this.loaddata();
      this.$refs.loadmore.onTopLoaded();
    },
    more() {
      console.log("more");
      // 分页查询
      this.pagesize ++;
      console.log("size",this.pagesize);
      this.loaddata();
      this.isHaveMore();

    },
    loaddata(type){
      console.log("loaddata");
      let params = {
        point_name: '',
        point_applicant: "",
        point_level: "",
        point_status: this.pointStatus,
        build_type_id: this.buildType,
        org_id: "",
        approval_role: "",
        point_submitter:"",
        page: this.pagesize
      };
      axios.post(this.searchUrl,params)
      .then((response)=> {
        let datas = response.data;
        console.log("point-evaluation/search-point-list response22",datas);
        if(datas.error_code==0){
          if(type==undefined) {
             if(this.pagesize == 0){
                this.pointList =datas.data.pointList.pointArray;
              }else{
                this.pointList = this.pointList.concat(datas.data.pointList.pointArray);
                if(datas.data.pointList.length == this.pageval){
                       this.haveMore = true;
                  }else{
                      this.haveMore = false;
                  }
            }
          } else if (type==1) {
            this.pointList = datas.data.pointList.pointArray;
            this.pagesize=0;
          }
        }else{
          this.alertMsg(datas.msg,'error');
        }
      })
      .catch((error)=> {
        console.log("error...",error);
        // this.alertMsg(error);
      });
    },
    isHaveMore: function () {
      // 是否还有下一页，如果没有就禁止上拉刷新
      this.allLoaded = true // true是禁止上拉加载
      if (this.haveMore) {
        this.allLoaded = false
      }
    }
  },
  components:{
  }
}
</script>
<style scoped>
  .yh-cont {
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-flex-direction: column;
    flex-direction: column;
    width: 100%;
    height: 100%;
  }
  .header-massage{
    width: 100%;
    height: 1.2rem;
    background-color: #bbb;
    position: relative;
  }
  .keyword-search input{
    width: 99%;
    height: 0.7rem;
  }
  .header-massage-first{
    position: absolute;
    top: 0.1rem;
    left: 0.1rem;
  }
  .header-massage-first span{
    position: relative;
    /*top: 0.15rem;*/
    left: 0.6rem;
  }
  .header-massage-first select{
    position: relative;
    top: 0.42rem;
    left: -0.5rem;
  }
  .header-massage-second{
    position: absolute;
    top: 0.09rem;
    left: 2.2rem;
  }
  .header-massage-second span{
    position: absolute;
    width: 100%;
    left: 1.1rem;
  }
  .header-massage-second select{
    position: relative;
    top: 0.45rem;
    left: 1rem;
  }
  .header-massage-third{
    position: absolute;
    top: 0.1rem;
    left: 5.4rem;
  }
  .header-massage-third span{
    position: absolute;
    /*width: 100%;*/
    left: 0.9rem;
  }
  .header-massage-third select{
    position: relative;
    top: 0.42rem;
    /*right: 0.05rem;*/
  }
  ul{
    list-style:none;
    margin-left: -0.7rem;
  }
  ul li{
    height: 0.8rem;
  }
  .search-result{
    position: relative;
    height: 4rem;
    width: 7.2rem;
    /*height: 100%;*/
    margin-bottom: 0.5rem;
    margin-left: 0.2rem;
    border-bottom: 1px solid #eee;
    /*padding-left: 0.2rem;
    padding-right: 0.2rem;*/
  }
  .record-status{
    position: absolute;
    top: 0.8rem;
    right: 0.3rem;
  }
  .see-detail{
    position: absolute;
    top: 3rem;
    right: 2.4rem;
    width: 1.5rem;
    height: 0.7rem;
    background-color: #fff;
    border-radius: 0.15rem;
    outline: none;
    border: 0;
  }
  .edit-data{
    position: absolute;
    top: 3rem;
    right: 0.3rem;
    width: 1.5rem;
    height: 0.7rem;
    background-color: #1e7bb3;
    border-radius: 0.15rem;
    outline: none;
    border: 0;
  }
  .searchBox{
    width: 100%;
    height: 0.52rem;
    position: relative;
  }
  .select{
    width: 98%;
    height: 100%;
  }
  .down{
    position: absolute;
    width: 0;
    border:0.18rem solid transparent;
    border-top: 0.22rem solid black;
    top: 0.2rem;
    right: 0.14rem;
    cursor: pointer;
    /*z-index: 10;*/
  }
  .list{
    width: 100%;
    /*border-bottom: 1px solid lightgray;*/
  }
  .list>li{
    height: 0.4rem;
    /*background: white;*/
    border-top: none;
  }
  .list-show{
    background-color: #fff;
    position: absolute;
    top: 0.55rem;
    z-index: 100;
    width: 100%;
    /*height: 4.3rem;*/
    overflow: auto;
  }
  .noInner{
    text-align: center;
    font-size: .26rem;
    color: #333;
    margin:.5rem 0;
  }
  .more-data{
    -webkit-box-flex: 1;
    -webkit-flex: 1;
    flex: 1;
    overflow: auto;
    -webkit-overflow-scrolling: touch;
    margin-top:.3rem;
  }
</style>



