<template>
  <div>
    <mt-search autofocus v-model="input">
      <mt-cell v-for="item in filterResult" :title="item.value" :key="item.id"  @click.native="selectBuild(item)">
      </mt-cell>
    </mt-search>
  </div>
</template>
<script>
/* eslint-disable */
// import Vue from 'vue'
  import axios from 'axios'
  // import { Search } from 'mint-ui';
  import { Indicator } from 'mint-ui'
  import { MessageBox } from 'mint-ui'
  export default {
  data () {
    return {
      input:"",//搜索文本
      showList:[],
      buildTypeId:"",
      buildList:[]
    }
  },
  computed: {
    filterResult() {
      return this.buildList.filter(item => new RegExp(this.input, 'i').test(item.value));
    }
  },
  watch: { // 监视双向绑定的数据数组
  },
  mounted() {
    this.init();
  },
  destroyed() {
  },
  methods: {
    init(){
      this.buildTypeId=this.$route.query.buildTypeId;//渠道ID
      console.log('typeid',this.buildTypeId);
      axios.get('/point-evaluation/get-build-list?build_type_id='+this.buildTypeId).then((res)=>{
        let initData = res.data;
        console.log("data",initData);
        if(initData.error_code ==0){
          if(initData.data.length>0){
             this.buildList=initData.data;//楼宇名称列表
          }else{
            this.alertMsg("暂无列表");
          }
          console.log("buildList",initData.data);
        }else{
          this.alertMsg("暂无列表");
        }
      })
      .catch((error)=>{
        console.log("error..",error);
      });
    },
    //选择搜索到的楼宇
    selectBuild(item){
      console.log("item",item);
      this.input=item.value;
      this.$router.push({
        name: 'pointNewAndEdit',
        query: {
          id: 2,
          buildId: item.id,
          buildName: item.value,
          buildTypeId: this.buildTypeId
        }
      });
    },
    alertMsg(msg,type)
    {
      let msgType = type?type:"error";
      MessageBox.alert('',{
        title: '',
        message: msg,
        confirmButtonText:'知道了'
      });
    },

  },
  components:{}
}
</script>
<style>
  body{
    background-color: #f9f9f9;
    font-size: .28rem;
  }
</style>
<style scoped>
 body{
    font-family: "微软雅黑";
    background: #f9f9f9;
  }
  a{
    text-decoration: none;
  }


</style>


