<template>
  <div class="content">
    <mt-cell>
      <div class="p1">联系人</div>
      <input type="text" class="p2" placeholder="请输入（选填）" v-model="form.contactName">
    </mt-cell>
    <mt-cell>
        <div class="p1">联系方式</div>
        <input type="text" class="p2" placeholder="请输入（选填）" v-model="form.contactTel">
    </mt-cell>
    <div class="btn-div">
        <button type="primary" @click="modifyData" class="created">修改</button>
    </div>
  </div>
</template>
<script>
/* eslint-disable */
  import axios from 'axios'
  import { Indicator } from 'mint-ui'
  import { MessageBox } from 'mint-ui'
  export default {
  data () {
    return {
      submitFormUrl:"/building-record/update-contact-info",
      form:{
        contactName:"",
        contactTel:"",
      },
      buildId:"",//楼宇Id
      pointId:"",//点位Id
    }
  },
  mounted() {
    this.init();
  },
  methods: {
    init(){
      this.buildId=this.$route.query.buildId;
      this.pointId=this.$route.query.pointId;
      this.form.contactName=this.$route.query.contactName;
      this.form.contactTel=this.$route.query.contactTel;
    },
    //修改数据
    modifyData(){
      if (!this.checkPhone(this.form.contactTel)){
        this.alertMsg('请填写正确的电话号码');
        return;
      }
      let params={
        contactName:this.form.contactName,
        contactTel:this.form.contactTel,
        id:this.buildId
      }
      console.log("params",params);
      axios.post(this.submitFormUrl,params)
        .then((response)=> {
          let data = response.data;
          console.log("response",response);
          if(data.error_code==0){
             this.alertMsg('修改成功','success');
             this.$router.push('/record');
          }else{
             this.alertMsg('修改失败','error');
          }
        })
        .catch((error)=> {
          this.alertMsg(error);
        });
    },
    checkPhone(value) {
      if (value==="") {
        return true;
      }else{
        var myReg=/^[1][3,4,5,6,7,8,9][0-9]{9}$/;//手机号验证
        var telReg=/^(\(\d{3,4}\)|\d{3,4}-|\s)?\d{7,14}$///座机号验证
        if (!myReg.test(value)&&!telReg.test(value)) {
           return false;
        }else{
           return true;
        }
      }
    },
    alertMsg(msg)
    {
      MessageBox.alert('',{
        title: '',
        message: msg,
        confirmButtonText:'知道了'
      });
    }
  }
}
</script>
<style scoped>
  .content {
    background-color: #fff;
    padding-top: 1rem;
    padding-bottom: .5rem;
  }
  .mint-cell .p1{
    font-size: .28rem;
    color: #333;
    display: inline-block;
    position: absolute;
    left: .5rem;
  }
  .mint-cell .p2{
    font-size: .28rem;
    color: #333;
    display: inline-block;
    position: absolute;
    right: .5rem;
    border: 0;
    background: #f9f9f9;
    outline: none;
  }
.created{
    display: block;
    width: 2.5rem;
    background-color: #fff;
    height: 1rem;
    border-radius: 0.2rem;
    outline: none;
    border: solid 1px #ddd;
    font-size:.28rem;
    margin:.5rem auto;
  }
  .btn-div {
    border-top: solid 1px #ccc;
  }

</style>


